@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap');

.mt {
    margin-top: 20px;
}

.mr {
    margin-right: 20px;
}

.mb {
    margin-bottom: 20px;
}

.ml {
    margin-left: 20px;
}

:root {
    font-size: 16px;
    @media screen and (max-width: 700px) {
            		font-size: 12px;
            	}
    @media screen and (max-width: 500px) {
        		font-size: 8px;
        	}
}

body {
    font-family: 'JetBrains Mono', monospace;
    padding-bottom: 60px;
    @media screen and (max-width: 700px) {
        		padding-bottom: 30px;
        	}
}

body * {
    font-family: inherit;
}

.title {
    max-width: 766px;
    text-align: center;
    font-size: 1.8rem;
    color: black;
    margin: 0 auto;
    line-height: 2.2rem;

    @media screen and (max-width: 400px) {
               		max-width: 300px;
               	}
}

header {
    padding: 60px 0;

    @media screen and (max-width: 700px) {
            		padding: 20px 0;
            	}
}

.container {
    max-width: 1120px;
    margin: 0 auto;
    padding: 0 20px;
    @media screen and (max-width: 400px) {
        		padding: 0 10px;
        	}
}

.grid-container {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-gap: 20px;

    @media screen and (max-width: 700px) {
    		grid-gap: 10px;
    	}
    @media screen and (max-width: 400px) {
        		grid-gap: 8px;
        	}

}

.card {
height: fit-content;
    position: relative;

    &:hover {
        .wrapper {
            opacity: 1;

        }
    }

    img {
        width: 100%;
        height: fit-content !important;
        object-fit: cover;
    }
}

.wrapper {
    top: 0;
    left: 0;
    position: absolute;
    opacity: 0;
    height: 100%;
    width: 100%;
    background: rgba(255,255,255,0.7);
    transition: 0.8s ease all;
    padding: 20px;

    @media screen and (max-width: 500px) {
        padding: 10px;
    }
}

.description {
    font-size: 1rem;
    line-height: 1.4rem;
    color: #000000;
    text-align: justify;
}

